/*
  ====================================================================
  Header
  ====================================================================
*/

.main-header {
    min-height: 850px;
    background: url("../img/content/start_bg.png") no-repeat;
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    width: 100%;
    position: relative;
    z-index: 0;
    //padding-bottom: 100px;
    box-sizing: border-box;
    opacity:0;
    &:after{
        content: '';
        position: absolute;
        background: $grey;
        width: 100%;
        height: 100%;
        max-height: 100%;
        background-size: cover;
        top: 0;
        left: 0;
        opacity: .9;
        min-width: 100%;
        z-index: 1;
    }
    &__logo {
        padding: 60px 0 70px 0;
        position: relative;
        z-index: 2;
    }

    &__title{
        padding-top: 100px;
        padding-left: 100px;
        margin: 0 auto;
        width: 820px;
        z-index: 2;
        position: relative;
        //opacity: 0;
        &:before{
            content: '';
            position: absolute;
            width: 0px;
            height: 2px;
            top: 190px;
            background: $main;
            animation-name: grounUP_line;
            animation-duration: 9s;
            animation-iteration-count: 1;
            animation-fill-mode: forwards;
        }
    }
    h4{
        color: $lightgrey;
        padding-top: 30px;
        position: relative;
    }
    &__button{
        padding-top: 70px;
        position: relative;
        //padding-left: 100px;
        .btn{
        color: $white;
        font-size: 14px;
        font-family: $Black;
        font-weight: 600;
        transition: $transition;
        letter-spacing: 2px;
        text-decoration: none;
        &:hover{
            color: $main;
            transition: $transition;
            text-decoration: none;
        }
        span{
            background: url("../img/content/arrow.png") no-repeat;
            padding-left: 25px;
            position: relative;
            left: 10px;
            top: 3px;
            }
        }
    }
    &__letter{
        //padding-left: 100px;
        font-size: 800px;
        font-family: $Black;
        color: $main;
        font-weight: 600;
        opacity: 0.6;
        position: absolute;
        top: -50px;
        left: 500px;
        z-index: 2;
    }
    &__social{
        z-index: 2;
        position: relative;
        ul{
        float: right;
        position: relative;
        top: 150px;
    }
        li{
            z-index: 2;
            position: relative;
            width: 23px;
            height: 30px;
            background: url("../img/content/social.png") no-repeat;
            transition: $transition;
            filter: grayscale(100%);
            cursor: pointer;
            margin: 20px 0;
            &:hover {
                transition: $transition;
                filter: grayscale(0);
            }
        }
    }

    .facebook{
        background-position: 0 0;
    }
    .twitter{
        background-position: 0 -58px;
    }
    .ball{
        background-position: 0 -120px;
    }
    .post{
        background-position: 0 -175px;
    }
    &__scroll{
        padding-top: 100px;
        z-index: 2;
        position: relative;
        width: 620px;
        margin: 0 auto;
        //opacity: 0;
        .scroll{
            color: $lightgrey;
            transition: $transition;
            letter-spacing: 2px;
            transform: rotate(90deg);
            -webkit-transform: rotate(90deg);
            -moz-transform: rotate(90deg);;
            text-decoration: none;

            transform-origin: left top 0;
            position: relative;
            left: 70px;
            &:hover{
                color: $main;
                transition: $transition;
                text-decoration: none;
            }
        }
        .mouse{
            background: url("../img/content/mouse.png") no-repeat;
            padding: 14px;
            position: relative;
            left: 10px;
            top: 3px;
            &:before{
                content: '';
                position: absolute;
                width: 2px;
                height: 227px;
                top: 39px;
                left: 9px;
                background: $lightgrey;
                z-index: 2;
            }
        }
    }
    &__letters{
        font-size: 200px;
        font-family: $Black;
        color: $grey;
        font-weight: 600;
        opacity: 0.7;
        z-index: 2;
        li{
            float: left;
            position: relative;
            &:first-child{
                right: 30px;
            }
            &:last-child{
                left: 80px;
            }
        }
    }
}
@media (max-width: 1050px) {
    .main-header {
        &__logo {
            padding: 60px 0 70px 10px;
        }
        &__title {
            padding-left: 0px;
            width: 100%;
            &:before{
                top: 170px;
            }
        }
        &__letter{
            font-size: 600px;
            top: 50px;
            left: 200px;
        }
        &__social ul{
            right: 20px;
        }
        &__scroll {
            width: 200px;
        }
    }
}
@media (max-width: 650px) {
    .main-header {
        min-height: 570px;
        &__title {
            padding: 0;
            &:before{
                top: 60px;
                width: 200px;
                left: 10px;
            }
        }
        h1,h4,.btn{
            padding-left: 10px;
        }

        &__letter{
            display: none;
        }
        &__letters{
            display: none;
        }
        &__social ul{
            top: 0;
        }

        &__scroll{
            padding-top: 20px;
        }
        .mouse:before {
            height: 138px;
        }
    }
}

