            }
            h3{
                padding-left: 10px;
            }
        }
        &__img:before {
            display: none;
        }
        &__item{
            padding-top: 10px;
            width: 100%;
            height: 100%;
        }
        &__text{
            width: 80%;
        }
    }
}
@media (max-width: 420px) {
    .blog {
        min-height: 600px;
        &__letters {
            font-size: 60px;
            left: 30px;
        }
        &__title {
            &:before {
                left: 10px;
            }
        }
        h3{
            letter-spacing: 8px;
        }
    }
}

/*
  ====================================================================
  Footer
  ====================================================================
*/
.main-footer{
    min-height: 700px;
    background: url("../img/content/contact_bg.png") no-repeat;
    background-position: center center;
    background-size: cover;
    position: relative;
    background-attachment: fixed;
    z-index: 0;
    box-sizing: border-box;
    width: 100%;
    &:after{
        content: '';
        position: absolute;
        background: $grey;
        width: 100%;
        height: 100%;
        max-height: 100%;
        background-size: cover;
        top: 0;
        left: 0;
        opacity: .9;
        min-width: 100%;
        z-index: 1;
    }
    &__item{
        float: left;
        width: 50%;
        z-index: 4;
        position: relative;
    }
    &__title {
        position: relative;
        padding: 120px 0 0 100px;
        z-index: 4;
        &:before {
            content: '';
            position: absolute;
            width: 400px;
            height: 2px;
            top: 185px;
            background: $main;
            z-index: 2;
        }
    }
    .info{
        min-height: 700px;
        position: relative;
        z-index: 3;


    }
    &__contacts{
        padding-top: 100px;
        width: 340px;
        margin: 0 auto;
        position: relative;
        left: 100px;
        z-index: 3;
        li{
            z-index: 2;
            background: url("../img/content/contacts.png") no-repeat;
            transition: $transition;
            filter: grayscale(100%);
            cursor: pointer;
            margin: 50px 0;
            &:hover {
                transition: $transition;
                filter: grayscale(0);
            }
        }
        a{

        }
    }


    .mail{
        background-position: 0 16px;
    }
    .phone{
        background-position: 0 -65px;
    }
    .address{
        background-position: 0 -154px;
    }
    h4{
        color: $lightgrey;
        font-size: 16px;
        transition: $transition;
        line-height: 20px;
        padding-top: 10px;
        &:hover{
            transition: $transition;
            color: $main;
        }


    }
    h5{
        color: $lightgrey;
        font-size: 16px;
        transition: $transition;
        a{
            color: $lightgrey;
            font-size: 16px;
            transition: $transition;
            text-decoration: none;
            &:hover{
                transition: $transition;
                color: $white;
            }
        }
        &:hover{
            transition: $transition;
            color: $white;
        }
    }
    iframe{
        width: 100%;
        height: 700px;
        position: relative;

    }
}

@media (max-width: 900px) {
    .main-footer {
        min-height: 400px;
        &__item {
            float: none;
            width: 100%;
        }
        &__title {
            &:before {
                width: 300px;
            }
        }
        &__contacts{
            width: 70%;
        }
        .info{
            min-height: 400px;

        }
    }
}
@media (max-width: 670px) {
    .main-footer {
        &__item {
            float: none;
            width: 100%;
        }
        &__title {
            padding: 50px 0 0 30px;
            &:before {
                width: 200px;
                top: 100px;
            }
        }
        &__contacts{
            padding: 0;
            width: 300px;
            left: 0;
        }
        h4,h5{
            padding-left: 40px;
        }
    }
}


.form{
    background: $white;
    height: 400px;
    width: 450px;
    margin: 0 auto;
    position: absolute;
    top: 250px;
    //left: 5%;
    &__list{
        margin: 0 auto;
        width: 580px;
    }
    &__item{
        float: left;
        width: 380px;
        padding-left: 45px;
    }
    input{
        width: 340px;
        height: 30px;
        margin-top: 20px;
        border: 1px solid $lightgrey;
        background: $white;
        padding-left: 10px;
        &:focus {
            outline: none;
        }
    }
    .form-textarea{
        width: 340px;
        height: 120px;
        margin-top: 20px;
        border: 1px solid $lightgrey;
        background: $white;
        padding-left: 10px;
        &:focus {
            outline: none;
        }
    }
    .small{
        margin-top: 30px;
        width: 155px;
        float: left;
        margin-right: 20px;
    }

    .form-btn{
        color: $white;
        padding: 11px 25px;
        background: $main;
        border: 3px solid $main;
        transition: $transition;
        margin-top: 35px;
        font-size: 12px;
        &:hover{
            background: transparent;
            border: 3px solid $main;
            transition: $transition;
            color: $main;
        }
        &:focus {
            outline: none;
        }
    }
    .message {
        padding: 10px;
        margin-bottom: 20px;
        background-color: #efa0a0;
        color: #cc5555;
        text-align: center;
        font-size: 14px;
        &:empty {
            display: none;
        }
    }
    .error{
        border: 1px solid $main;
    }
    label.error {
        width: 0px;
        //display: block;
        padding-left: 10px;
        font-size: 0px;
        border: none;
        color: $main;
        position: absolute;
        //left: 100%;
        //top: 10px;
        z-index: 1;
        display: none;
    }
}

@media (max-width: 670px) {
    .form {
        background: $white;
        height: 400px;
        width: 100%;
        margin: 0 auto;
        position: absolute;
        top: 350px;
        left: 0;
        &__list {
            margin: 0 auto;
            width: 80%;
        }
        &__item {
            float: none;
            width: 100%;
            padding-left: 0px;
            //margin: 0 auto;
            //text-align: center;
        }

        input {
            width: 255px;
        }
        .small{
            width: 255px;
        }
        .form-textarea {
            width: 255px;
            height: 60px;
        }
    }
}
