// GUI stylies of the project
// For example, styles of the links, buttons and so on.


.container{
    width: 1200px;
    margin: 0 auto;
    height: 100%;
    position: relative;
    box-sizing: border-box;
    @include respond-to(tablet){
        width: 100%;
        //padding: 0 20px;
    }
    @include respond-to(mobile){
        width: 100%;
    }

}

.navigation{
    position: relative;
    z-index: 2;
    top: 50px;
    ul{
        position: relative;
        width: 170px;
        float: left;
    }
    li {
        z-index: 2;
        position: relative;
        cursor: pointer;
    }
    a{
        color: $white;
        font-size: 60px;
        font-family: $Black;
        letter-spacing: 10px;
        transition: $transition;
        text-decoration: none;
        &:hover{
            transition: $transition;
        //    color: $main;
            text-decoration: none;
        //    .navigation__point{
        //        background: $main;
        //        transition: $transition;
        //    }
        }
    }
    &__point{
        width: 15px;
        height: 15px;
        border-radius: 100%;
        background: $white;
        position: relative;
        left: 25px;
        top: 40px;
    }
    &__text{
        position: relative;
        left: 50px;
        top: 3px;
        letter-spacing: 1px;
        width: 140px;
    }
    .active{
        margin-bottom: 160px;
        &:before{
            content: '';
            position: absolute;
            width: 3px;
            height: 100px;
            top: 39px;
            left: 7px;
            background: $white;
        }
    }
    .passive{
        background: $lightgrey;
        transition: $transition;
        margin-top: 30px;

        &:hover{
            background: $white;
            transition: $transition;

        }
    }
    .black{
        color: $black;
        background: $black;
        &:before {
            background: $black;
        }
    }
    .dark{
        color: $black;
    }
    .first{
        margin-bottom: 70px;
    }
}

@media (max-width: 650px) {
    .navigation{
        display: none;
    }
}

.hideme{
    opacity: 0;
}

@keyframes grounUP_line {
    10% {width: 0;}
    100% {width: 230px;}
}

